  .remodal-close {
    width: 35px !important;
    height: 30px !important;
  }
}
@media only screen and (min-width: 320px) {
  .remodal-close:before {
    font-size: 30px !important;
    top: -7px !important;
    left: -12px !important;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-close:before {
    font-size: 50px !important;
    top: 0px !important;
    left: 0px !important;
  }
}
.remodal-common {
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  background: #fff;
  padding: 30px 40px;
}
@media only screen and (min-width: 320px) {
  .remodal-common {
    max-width: 500px !important;
    padding: 40px 15px;
    margin: 0 auto;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  .remodal-common {
    padding: 40px 20px;
    max-width: 450px !important;
    text-align: center;
  }
}
.remodal-common .remodal-close {
  color: #000 !important;
}
.error-input {
  clear: both;
  float: left;
  font-size: 13px;
  padding-left: 93px;
  color: #df3a3a;
  margin: 0 auto;
  text-align: center;
}
input:focus:required:invalid,
textarea:focus:required:invalid {
  border-bottom: 1px solid #909091;
}
input:required:valid,
textarea:required:valid {
  border-bottom: 1px solid #909091;
}
#name-error {
  font-size: 0 !important;
}
.input {
  position: relative;
}
label {
  position: absolute;
  top: 0;
  border: 0;
  font-size: 0 !important;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}
.formBox {
  position: relative;
  background: #fff;
  padding: 30px 15px;
  max-width: 400px;
  margin: 20px auto;
  text-align: center;
}
.formBox .form__line {
  width: 100%;
  padding-left: 15px;
  padding-bottom: 7px;
  border: none!important;
  outline: none;
  font-size: 18px;
}
.formBox .btn {
  margin: 15px 0 5px 0;
  color: #fff;
}
.formBox .btn:hover {
  color: #000;
}
.video {
  max-width: 780px;
}
.video iframe {
  border: none;
  width: 100%;
  height: 100%;
}
.thanks__title {
  padding-bottom: 15px;
  font-size: 22px;
  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}
.thanks__text {
  display: block;
  padding-bottom: 15px;
  padding-left: 0;
  font-size: 18px;
  color: #000;
}
@keyframes astro-bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-7px);
  }
}
@keyframes smoothScroll {
  0% {
    transform: translateY(-40px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
.slick-prev,
.slick-next {
  font-family: 'Fontello';
  transition: all .3s linear;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.slick-prev {
  left: -10px;
}
.slick-next {
  right: 0;
}
.slick-prev:before,
.slick-next:before {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-25%);
  z-index: 10;
  color: #fff;
  font-size: 60px;
}
@media only screen and (min-width: 1600px) {
  .slick-prev:before,
  .slick-next:before {
    font-size: 120px;
  }
}
.slick-prev:before {
  left: 0;
}
.slick-next:before {
  right: 0;
}
.slick-dots {
  left: 50%;
  bottom: -40px;
  transform: translateX(-50%);
  border-image: transparent;
}
.slick-dots li {
  margin: 0 5px;
  width: 20px;
  height: 20px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 20px;
  width: 20px;
  font-size: 0px;
  color: #fff;
  padding: 5px;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
